import { CardList, Pagination } from 'components';
import Loader from 'components/Loader/Loader';
import { useEffect, useState } from 'react';
import { useFetchSearchQuery } from 'redux/operations';
import {
  FoldedContainer,
  SearchAsideCatalog,
  SearchBrands,
  SearchCategoryList,
  SearchContainer,
  SearchDescriptionSpan,
  SearchDesriptionResults,
  SearchFilter,
  SearchQuery,
  SearchTravelBag,
  SearchWrapperCatalog,
  SortingContainer,
  TitleSearch,
  UpsideSearchContainer,
} from './Search.styled';
import { RightArrow } from 'components/Icons';
import { useSelector } from 'react-redux';
import { PriceSlider } from 'components/PriceSlider/PriceSlider';
import React from 'react';
import { Filter } from 'components/Filter/Filter';
import { SortSelect } from 'components/Filter/SortSelect';
import { theme } from 'styles';
import { selectSearchQueryStore, selectSortingTypeStore } from 'redux/selectors';
import { SearchBar } from 'components/SearchBar/SearchBar';

export const Search = () => {
  const [productsList, setProductsList] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [paginationData, setPaginationData] = useState({
    hasNextPage: false,
    hasPrevPage: false,
    limit: null,
    nextPage: null,
    page: 1,
    prevPage: null,
    totalPages: null,
  });
  const [loadMoreProducts, setLoadMoreProducts] = useState([]); //    ,   "Load More"
  const [loadMoreClicked, setLoadMoreClicked] = useState(false); //        "Load More"
  const [active, setActive] = useState({ price: false, brands: false });

  const searchQuery = useSelector(selectSearchQueryStore);
  const sortingType = useSelector(selectSortingTypeStore);

  const {
    data: response,
    error,
    isLoading,
    isFetching,
  } = useFetchSearchQuery({
    query: searchQuery,
    sorting: sortingType ? `&sortBy=${sortingType}` : '',
  });

  useEffect(() => {
    if (response) {
      async function fetchInitialData() {
        if (loadMoreClicked) {
          setLoadMoreProducts(prevLoadMoreProducts => [...prevLoadMoreProducts, ...response.docs]);
          const pagination = {
            hasNextPage: response.hasNextPage,
            hasPrevPage: response.hasPrevPage,
            limit: response.limit,
            nextPage: response.nextPage,
            page: response.page,
            prevPage: response.prevPage,
            totalPages: response.totalPages,
          };
          setPaginationData(pagination);
        } else {
          setProductsList([...response.docs]);
          setLoadMoreProducts([...response.docs]);
          setLoadMoreClicked(false);

          const pagination = {
            hasNextPage: response.hasNextPage,
            hasPrevPage: response.hasPrevPage,
            limit: response.limit,
            nextPage: response.nextPage,
            page: response.page,
            prevPage: response.prevPage,
            totalPages: response.totalPages,
          };
          setPaginationData(pagination);
        }
      }

      if (!isFetching) {
        fetchInitialData();
      }
    }
  }, [isFetching, loadMoreClicked, response]);

  const onPageChange = pageNumber => {
    //       ,  
    setLoadMoreClicked(false);
    setCurrentPage(pageNumber);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const onAddPage = async () => {
    //     "Load More",  
    setLoadMoreClicked(true);
    setCurrentPage(currentPage + 1);
  };

  const handleClickToggle = e => {
    active[e.currentTarget.name]
      ? setActive({ ...active, [e.currentTarget.name]: false })
      : setActive({ ...active, [e.currentTarget.name]: true });
  };

  return (
    <>
      {error?.status >= 500 ? (
        <>Oops, there was an error ...</>
      ) : isLoading ? (
        <Loader />
      ) : error?.status < 500 ? (
        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
          <div style={{ marginTop: '48px' }}>
            <SearchDesriptionResults style={{ fontSize: '24px', width: '736px' }}>
              <SearchDescriptionSpan>  </SearchDescriptionSpan>
              <SearchQuery>{searchQuery}</SearchQuery>
              <SearchDescriptionSpan>    </SearchDescriptionSpan>
            </SearchDesriptionResults>
            <div style={{ marginTop: '180px', width: '520px' }}>
              <div style={{ marginBottom: '20px' }}>  ,   :</div>
              <SearchBar />
            </div>
          </div>

          <SearchTravelBag />
        </div>
      ) : response?.docs.length > 0 ? (
        <>
          <UpsideSearchContainer>
            <TitleSearch> </TitleSearch>
            <SearchDesriptionResults>
              <SearchDescriptionSpan>  </SearchDescriptionSpan>
              <SearchQuery>{searchQuery}</SearchQuery>
              <SearchDescriptionSpan>  </SearchDescriptionSpan>
              <SearchQuery>{response.totalDocs} </SearchQuery>
              <SearchDescriptionSpan>
                {response.totalDocs === 1 ? '' : response.totalDocs < 5 ? '' : ''}
              </SearchDescriptionSpan>
            </SearchDesriptionResults>
            <SortingContainer>
              <SortSelect />
            </SortingContainer>
          </UpsideSearchContainer>

          <SearchContainer>
            <SearchAsideCatalog>
              <SearchCategoryList>
                <ul
                  style={{
                    display: 'flex',
                    flexDirection: 'column',
                    gap: '4px',
                  }}
                >
                  <li key={1}>
                    <SearchFilter active={active['price']}>
                      <FoldedContainer
                        active={active['price']}
                        style={{
                          backgroundColor: active['price']
                            ? theme.colors.secGreen
                            : theme.colors.beige,
                        }}
                      >
                        <span></span>
                        <button onClick={handleClickToggle} name="price">
                          <RightArrow direction={active['price'] ? 'rotate(90)' : 'rotate(-90)'} />
                        </button>
                      </FoldedContainer>
                      <PriceSlider active={active['price']} />
                    </SearchFilter>
                  </li>
                  <li key={2}>
                    <SearchBrands activeBrands={active['brands']}>
                      <FoldedContainer
                        style={{
                          backgroundColor: active['brands']
                            ? theme.colors.secGreen
                            : theme.colors.beige,
                        }}
                      >
                        <span></span>
                        <button onClick={handleClickToggle} name="brands">
                          <RightArrow direction={active['brands'] ? 'rotate(90)' : 'rotate(-90)'} />
                        </button>
                      </FoldedContainer>
                      <Filter active={active['brands']} />
                    </SearchBrands>
                  </li>
                </ul>
              </SearchCategoryList>
            </SearchAsideCatalog>

            <SearchWrapperCatalog>
              <CardList
                productsList={
                  currentPage === 1
                    ? productsList
                    : loadMoreClicked
                    ? loadMoreProducts
                    : productsList
                }
              />
              <Pagination
                paginationData={paginationData}
                onPageChange={onPageChange}
                onAddPage={onAddPage}
              />
            </SearchWrapperCatalog>
          </SearchContainer>
        </>
      ) : null}
    </>
  );
};
